<template>
  <div class="category-card">
    <router-link :to="getDetailPageUrl" class="card-link">
      <img :src="getPosterUrl(cardData.poster_path)" alt="Poster" class="poster" />
      <div class="card-content">
        <h3>{{ cardData.title || cardData.name }}</h3>
        <p>{{ cardData.overview }}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  props: {
    cardData: {
      type: Object,
      required: true
    }
  },
  methods: {
    getDetailPageUrl() {
      if (this.cardData.title) {
        return `/movie/${this.cardData.id}`;
      } else {
        return `/tv/${this.cardData.id}`;
      }
    },
    getPosterUrl(path) {
      return `https://image.tmdb.org/t/p/w500${path}`;
    }
  }
};
</script>

